const searchAndAddStrings = (data) => {
  let results = [];

  if (typeof data === "object") {
    Object.keys(data).forEach((key) => {
      if (typeof data[key] === "string") {
        results.push(data);
      } else {
        const nestedResults = searchAndAddStrings(data[key]);
        results = results.concat(nestedResults);
      }
    });
  }

  return results;
};

const peopleData = searchAndAddStrings(companyData);

export default peopleData;
